.offer_strip {
       z-index: 999;
       right: 0;
       left: 0;
       max-width: 540px;
       width: 100%;
       bottom: 0;
       box-shadow: 0 0 3.6rem rgba(0, 0, 0, 0.12);
}

@media (min-width: 991px) {
       .offer_strip {
              left: 83px;
       }
}

@keyframes fadeInUp {
       0% {
              transform: translateY(100%);
              opacity: 0;
       }

       100% {
              transform: translateY(0%);
              opacity: 1;
       }
}

.offer_strip.show {
       animation: 1s fadeInUp;
}


.header-fixed.header-mobile-fixed #ugc-suit-influencer .subheader {
       top: 50px;
       background-color: var(--bs-body-bg) !important;
}

.rotate-animation {
    -webkit-animation: animation-spinner .7s linear infinite;
    animation: animation-spinner .7s linear infinite;
}




.onsite_preview_inner .t_modal_wrap {
       position: relative;
       z-index: 1;
       background-color: transparent;
}
.onsite_preview_inner .t_modal {
       transform: scale(0.81);
       width: 124%;
       height: 124%;
       margin-left: -12%;
       margin-right: -12%;
       margin-top: -12%;
       margin-bottom: -12%;
       z-index: 1;
       overflow: hidden;
}

.subtree-container .t_modal .t_modal_wrap{
       z-index: 2;
       position: absolute;
       left: 12px;
       top: 115px;
       width: calc(100% - 24px);
       height: calc(100% - 208px);
}

@media (min-width: 991px) {
.subtree-container .t_modal .t_modal_wrap{
       left: 111px;
       top: 120px;
       width: calc(100% - 138px);
}
}
@media (min-width: 1280px) {
       .subtree-container .t_modal .t_modal_wrap{
              left: 431px;
              width: calc(100% - 458px);
       }
}

.t_overlay_{
       display: none;
}
