.exceed-section {
    font-weight: 400;
    font-size: 14px;
    color: #637079;

    .page-title {
        margin-bottom: 20px;

        h3 {
            color: #000;
            font-size: 20px;
        }

    }

    .exceed-box {
        border: 1px solid var(--bs-border-color);
        background-color: var(--bs-body-bg);
        border-radius: 8px;
        padding: 40px 30px;

        .mw-50 {
            max-width: 50%;
        }

        >strong {
            // font-size: 16px;
            font-size: 20px;
            font-weight: 500;
            color: var(--bs-primary);
            margin-bottom: 5px;
            display: block;
        }

        .exceed-inn {
            margin-top: 25px;
            margin-bottom: 25px;

            >div {

                // background-color: var(--bs-body-bg);
                // border-radius: 4px;
                // padding: 10px 20px;
                // min-width: 176px;
                &.btn-outline-primary {
                    &:hover {
                        strong {
                            color: var(--bs-body-bg);
                        }

                        img {
                            filter: brightness(0) invert(1);
                        }
                    }
                }

                strong {
                    // font-size: 14px;
                    font-weight: 500;
                    // display: block;
                    text-transform: capitalize;
                    color: #1A232F;
                    vertical-align: middle;
                }
            }

            >span {
                font-size: 12px;
                font-weight: 300;
                color: #637079;
                display: block;
                margin-top: 15px;
            }
        }

        .unlock-actions {
            .btn {
                font-size: 14px;
                font-weight: 500;
                height: 40px;
                padding-top: 7.5px;
            }
        }
    }
}

.exceed-accordion {
    border: 1px solid var(--bs-border-color);
    background-color: var(--bs-body-bg);
    border-radius: 8px;
    padding: 40px 30px;

    .exceed-accordion-title {
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid var(--bs-border-color);


        p {

            span {
                font-weight: 700;
            }
        }
    }

    #planAccordion {
        .accordion {
            .accordion-item {
                border: 0;

                .accordion-header {
                    >button {
                        padding: 15px 20px;
                        background-color: var(--bs-gray-100);

                        &::after {
                            left: 20px;
                            top: 23px;
                        }
                    }

                }

                .planfeed-list-head,
                .card-body {
                    background-color: var(--bs-secondary-200);
                }

                .planfeed-list-head {
                    .col-sm-8 {
                        p {
                            padding-left: 15px;
                        }
                    }

                    .col-sm-4 {
                        p {
                            padding-right: 20px;
                        }
                    }
                }

                .planfeed-list-body {
                    .planfeed-list-row {
                        padding-top: 15px;
                        padding-bottom: 15px;

                        .btn-clean {
                            margin-right: 10px;
                            opacity: 0.6;
                            transition: all 0.3s ease-in-out;

                            &:hover {
                                opacity: 1;
                            }
                        }
                    }

                    .planfeed-list-row.unlock-net-highlighter {
                        background: rgba(255, 64, 80, 0.06);
                        position: relative;

                        &::before {
                            content: '';
                            background: rgba(255, 64, 80, 0.06);
                            width: 15px;
                            position: absolute;
                            left: -15px;
                            top: 0;
                            height: 100%;
                        }

                        &::after {
                            content: '';
                            background: rgba(255, 64, 80, 0.06);
                            width: 15px;
                            position: absolute;
                            right: -15px;
                            top: 0;
                            height: 100%;
                        }

                        .symbol {
                            &::before {
                                content: '\f05a';
                                position: absolute;
                                right: -10px;
                                top: -10px;
                                // width: 13px;
                                // background: url(https://cloud.tagbox.com/react-app/media/exceed-limit/information-red.svg) no-repeat;
                                // height: 13px;
                                // background-size: contain;
                                // border: 1px solid #fff;
                                // border-radius: 50%;
                                font-family: "Font Awesome 6 Pro";
                                font-weight: 900;
                                color: var(--bs-primary);
                            }
                        }
                    }


                    .widget-name {
                        span {
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }
                    }
                }

                .planfeed-list-row+.planfeed-list-row {
                    border-color: #DCE0E3;
                }
            }
        }

        .plan_restrict_count {
            strong {
                font-size: 16px;
                font-weight: 500;
                color: #000000;

                span {
                    border: 1px solid #DCE0E3;
                    background-color: #F6F9FB;
                    border-radius: 4px;
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    vertical-align: middle;
                    line-height: 38px;
                    margin-left: 10px;
                    margin-right: 10px;
                }
            }
        }

        .custom-accordion {
            margin-bottom: 2px;

            .cust-acc-head {
                padding: 15px;
                border-radius: 4px;
                background-color: var(--bs-gray-1000);

                p {
                    margin-bottom: 0;
                }

                .accordion-cust-col {
                    display: flex;
                    align-items: center;
                }
            }

            .cust-acc-body {
                .card {
                    box-shadow: none;

                    .planfeed-list-row {
                        padding: 10px 15px;
                        background-color: var(--bs-gray-100);

                        .row {
                            position: relative;

                            &::after {
                                content: "";
                                left: 12px;
                                right: 12px;
                                bottom: -10px;
                                height: 1px;
                                position: absolute;
                                background-color: #eee;
                            }

                            .col-3 {
                                display: flex;
                                align-items: center;
                            }
                        }

                        &:last-child {
                            .row {
                                &::after {
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media(max-width:1299px) {
    .exceed-section {
        padding-left: 10px;
        padding-right: 10px;
    }
}

@media(max-width:1199px) {
    .exceed-section .page-title h3 {
        font-size: 18px;
    }

    .exceed-accordion #planAccordion .plan_restrict_count strong span {
        width: 36px;
        height: 36px;
        line-height: 34px;
        margin-left: 7px;
        margin-right: 7px;
    }
}

@media(max-width:991px) {

    .exceed-section .exceed-box,
    .exceed-accordion {
        padding-top: 30px;
        padding-bottom: 30px;
    }

    .exceed-section .exceed-box .exceed-inn>div strong {
        font-size: 16px;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .accordion-header>button .btn-clean {
        width: 32px;
        height: 32px;
    }

    .exceed-section .exceed-box .mw-50 {
        max-width: 100%;
    }
}

@media(max-width:767px) {
    .exceed-section .page-title {
        margin-bottom: 0;
        margin-top: 5px;
    }

    .exceed-section .exceed-box,
    .exceed-accordion {
        padding: 20px;
    }

    .exceed-section .exceed-box .exceed-inn {
        margin-top: 20px;
        margin-bottom: 25px;
    }

    .exceed-accordion .exceed-accordion-title {
        padding-bottom: 10px;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .accordion-header h6,
    .exceed-accordion #planAccordion .accordion .accordion-item .accordion-header span {
        font-size: 14px !important;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .accordion-header>button .btn-clean {
        width: 28px;
        height: 28px;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .accordion-header>button .btn-clean img {
        width: 16px;
        height: 16px;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .planfeed-list-body .planfeed-list-row .btn-clean {
        margin-right: 6px;
    }

    .exceed-accordion #planAccordion .plan_restrict_count strong {
        font-size: 14px;
    }

    .exceed-accordion #planAccordion .plan_restrict_count strong span {
        width: 30px;
        height: 30px;
        line-height: 28px;
        margin-left: 5px;
        margin-right: 5px;
    }
}

@media(max-width:575px) {
    .exceed-accordion #planAccordion .accordion .accordion-item .accordion-header>button .px-0 .d-flex {
        flex-wrap: wrap;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .accordion-header span {
        margin-bottom: 2px;
        display: block;
    }
}

@media(max-width:479px) {

    .exceed-section,
    .exceed-accordion .exceed-accordion-title p {
        font-size: 13px;
    }

    .exceed-section .exceed-box>strong,
    .exceed-accordion .exceed-accordion-title strong {
        font-size: 14px;
    }

    .exceed-section .exceed-box .exceed-inn {
        margin-top: 10px;
        margin-bottom: 20px;
    }

    .exceed-section .exceed-box .exceed-inn>div {
        padding: 6px 20px;
        min-width: inherit;
    }

    .exceed-section .exceed-box .exceed-inn>div strong {
        font-size: 15px;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .accordion-header>button {
        padding: 10px 15px;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .planfeed-list-body .planfeed-list-row {
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .accordion-header>button::after {
        left: 15px;
        top: 16px;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .planfeed-list-body .planfeed-list-row .btn-clean {
        margin-right: 0;
    }

    .exceed-accordion #planAccordion .plan_restrict_count strong span {
        width: 26px;
        height: 26px;
        line-height: 24px;
    }

    .exceed-section .exceed-box,
    .exceed-accordion {
        padding: 15px;
    }

    .exceed-section {
        padding-left: 0;
        padding-right: 0;
    }

    .exceed-accordion .accordion .accordion-item .accordion-cust-col {
        width: 100%;
    }

    .exceed-accordion .accordion .accordion-item .accordion-cust-del {
        position: absolute;
        right: 5px;
        top: 15px;
    }

    // #planAccordion .card .planfeed-list-head,
    .exceed-accordion #planAccordion .card .planfeed-list-body {
        // padding: 5px 0;
        padding: 0;
    }

    .exceed-accordion #planAccordion .card .planfeed-list-body .planfeed-list-row>div>div {
        padding-left: 0;
        padding-right: 0;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .planfeed-list-body .totol-post {
        font-size: 12px;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .planfeed-list-body .totol-post>span {
        display: block;
    }

    .exceed-accordion #planAccordion .plan_restrict_count strong {
        font-size: 13px;
    }

    .exceed-accordion #planAccordion .accordion .accordion-item .planfeed-list-body .planfeed-list-row.unlock-net-highlighter::before,
    .exceed-accordion #planAccordion .accordion .accordion-item .planfeed-list-body .planfeed-list-row.unlock-net-highlighter::after {
        display: none;
    }
}

@media(max-width:374px) {
    .exceed-section .exceed-box .unlock-actions .btn {
        font-size: 12px;
    }
}