.content_inner {
    display: flex;
    align-items: start;
}

.theme_preview_wrap {
    height: calc(100vh - 240px);
    overflow: hidden;
}

.theme_preview {
    transform: scale(0.8);
    width: 125%;
    margin-left: -12.5%;
    height: calc(100vh + 16.7vh - 254px);
    margin-top: -8vh;
    margin-bottom: -8vh;
}

.theme_preview iframe {
    border: none;
    width: 100%;
    height: 100%;
    // background-color: var(--bs-white);
    background-color: var(--bs-transparent);
}

.aside__ {
    width: 320px;
    display: flex;
    flex-shrink: 0;
}

.preview_ {
    flex-grow: 1;
    flex-direction: column;
    border: none;
    box-shadow: none;
}

.aside__.offcanvas {
    top: 50px;
    height: calc(100vh - 50px);
    z-index: 95;
}

.aside__ .card {
    border: none;
    box-shadow: none;
}

.aside__ .accordion-button::after {
    height: 1rem;
    width: 1rem;
    background-size: contain;
}

.aside__ .a_header_ .card-header,
.preview_.p_header_ .card-header,
.aside__ .a_footer_ .card-footer,
.preview_.p_footer_ .card-footer {
    height: 65px;
    border-top-right-radius: 0;
}

.aside__ .card-body,
.preview_ .card-body {
    height: calc(100vh - 80px);
    overflow-y: auto;
    overflow-x: hidden;
}


.aside__ .a_header_ .card-body,
.preview_.p_header_ .card-body,

.aside__ .a_footer_ .card-body,
.preview_.p_footer_ .card-body {
    height: calc(100vh - 198px);
}

.aside__ .a_footer_.a_header_ .card-body,
.preview_.p_footer_.p_header_ .card-body {
    height: calc(100vh - 272px);
}



.has-subheader .aside__ .card-body,
.has-subheader .preview_ .card-body {
    height: calc(100vh - 145px);
    overflow-y: auto;
    overflow-x: hidden;
}


.has-subheader .aside__ .a_header_ .card-body,
.has-subheader .preview_.p_header_ .card-body,

.has-subheader .aside__ .a_footer_ .card-body,
.has-subheader .preview_.p_footer_ .card-body {
    height: calc(100vh - 208px);
}

.has-subheader .aside__ .a_footer_.a_header_ .card-body,
.has-subheader .preview_.p_footer_.p_header_ .card-body {
    height: calc(100vh - 272px);
}

.shopon_bio .aside__ .a_footer_.a_header_ .card-body{
    height: calc(100vh - 164px);
}

.container-content-gallery .sync_products .publish-card .dropdown button.show {
    background-color: rgba(var(--bs-primary-rgb), 0.05);
}
@media (min-width: 992px) {
    .aside__.offcanvas {
        left: 84px;
    }

    .aside__ .a_header_ .card-header,
    .preview_.p_header_ .card-header,
    .aside__ .a_footer_ .card-footer,
    .preview_.p_footer_ .card-footer {
        height: 65px;
        border-top-right-radius: 0;
    }

    .aside__ .card-body,
    .preview_ .card-body {
        height: calc(100vh - 92px);
        overflow-y: auto;
        overflow-x: hidden;
    }


    // .aside__ .a_header_ .card-body,
    .preview_.p_header_ .card-body,
    .aside__ .a_footer_ .card-body

    /*,.preview_.p_footer_ .card-body*/
        {
        height: calc(100vh - 205px);
    }

    .aside__ .a_header_ .card-body {
        height: calc(100vh - 163px);
    }

    .custom-schema-main.preview_.p_header_ .card-body {
        height: calc(100vh - 205px);
    }

    .preview_.p_footer_ .card-body {
        height: calc(100vh - 100px);
    }

    .aside__ .a_footer_.a_header_ .card-body,
    .preview_.p_footer_.p_header_ .card-body {
        height: calc(100vh - 278px);
    }

}


@media (max-width: 991px) {

    .aside__ .a_footer_.a_header_ .card-body,
    .preview_.p_footer_.p_header_ .card-body,
    .has-subheader .aside__ .a_footer_.a_header_ .card-body,
    .has-subheader .preview_.p_footer_.p_header_ .card-body {
        height: calc(100vh - 230px);
    }
    .shopon_bio .aside__ .a_footer_.a_header_ .card-body{
        height: calc(100vh - 160px);
    }

}