$border-color-translucent: #dadce0;

$primary-color: #4179ff;

$border-radius: 8px;

$prefix: bs- !default;
$prefix: $variable-prefix !default;

$font-weight-lighter: 200;
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-bold: 600;
$font-weight-bolder: 700;

$h6-font-weight: '700';

$font-family-base : 'Inter',
sans-serif;

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1280px,
  xxl: 1600px
);

$container-max-widths: (
  sm: 100%,
  md: 87.5rem,
  lg: 87.5rem,
  xl: 87.5rem,
  xxl: 87.5rem
);

@import './variables';
@import "./_mixin/_utilities";
@import "./_mixin/_reboots";
@import "~bootstrap/scss/bootstrap";

$theme-colors: (
  "primary": $primary,
  "secondary": $secondary,
  "success": $success,
  "info": $info,
  "warning": $warning,
  "danger": $danger,
  "light": $light,
  "dark": $dark
);

