      font-size: 12px;
      color: var(--bs-gray-700);
    }

    button {
      border: 0;
      padding: 0;
      color: var(--bs-gray-700);

      &::after {
        content: '\f078';
        font-family: "Font Awesome 6 Pro";
        border: 0;
        font-size: 11px;
      }

      span {
        font-size: 12px !important;
      }

      .dropdown-menu,
      .dropdown-menu.show {
        min-width: 220px;
      }

      .dropdown-toggle {
        .fw-semibold {
          font-weight: normal !important;
        }

      }
    }

    &::before,
    &+.breadcrumb-item::before {
      content: var(--bs-breadcrumb-divider, "");
    }

    &+.breadcrumb-item::before {
      content: var(--bs-breadcrumb-divider, ".");
      --bs-heading-color: var(--bs-gray-900);
      font-size: map-get($display-font-sizes, 4);
      top: -7px;
      position: relative;
    }

    .menu-icon {

      i,
      img {
        width: 32px;
        height: 32px;
        // border-radius: $border-radius;
        font-size: map-get($display-font-sizes, 8);
      }

      i {
        background-color: var(--bs-gray-100);
        color: var(--bs-gray-900);
        // border-radius: $border-radius;
      }

      img {
        object-fit: cover;
      }

      strong {
        font-weight: var(--bs-font-weight-bold);
        color: var(--bs-gray-900);

        // font-size: map-get($display-font-sizes, 8);
        &.text-truncate {
          max-width: 180px;
        }
      }
    }

    .dropdown-menu.show {
      min-width: 220px;
    }
  }
}

[data-product="taggbox"] input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  background: #ff4051;
}

[data-product="taggbox"] .btn-secondary.btn-primary,
[data-product="tagembed"] .btn-secondary.btn-primary {
  --bs-btn-color: var(--bs-white);

  &:hover,
  &:focus {
    --bs-btn-color: var(--bs-white);
    color: var(--bs-white);
  }
}

[data-theme=dark] .header-main .header_logo img {
  filter: invert(1);
}

@media (min-width: 992px) {
  .modal-md {
    --bs-modal-width: 630px;
  }
}

@media (min-width: 576px) {
  .modal-sm {
    --bs-modal-width: 470px;
  }

  .modal-xs {
    --bs-modal-width: 320px;
  }
}

.nav-tabs {
  --bs-nav-tabs-border-radius: 0;
}

@media(max-width:991px) {

  .app-page.has-subheader.has-crumbs .main_content .sub-crumbs,
  .sub-crumbs .btn-primary.btn-lg {
    padding: 5px 10px;
    font-size: 13px;
  }

  .breadcrumb .breadcrumb-item .menu-icon strong {
    max-width: 160px;
  }
}

@media(max-width:575px) {
  .breadcrumb .breadcrumb-item .menu-icon strong {
    // font-size: 13px;
    font-size: 12px !important;
  }

  .breadcrumb .breadcrumb-item+.breadcrumb-item::before {
    padding-right: 2px;
  }

  .breadcrumb-item+.breadcrumb-item {
    padding-left: 5px;
  }

  .sub-crumbs .btn-min-w {
    min-width: auto;
  }

  .publish-card .browser i,
  .publish-card .link i,
  .publish-card .bullhorn i {
    font-size: 20px;
  }

  .breadcrumb .breadcrumb-item .menu-icon i,
  .breadcrumb .breadcrumb-item .menu-icon img {
    width: 26px;
    height: 26px;
    font-size: 12px;
  }

  .breadcrumb .breadcrumb-item .menu-icon strong {
    max-width: 120px;
  }
}

