.post-scheduler-container{
    padding-top: 50px;
    .aside__body{
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .aside__body > div.card-aside{
        height: calc(100vh - 155px);
        overflow: auto;
    }

    .aside__ .card{
        border-radius: 0px;
    }
    .publish-content .full-content{
        padding-top: 50px;
        padding-bottom: 0;
    }
    .post-heading{
        color: #A3AABD;
        font-weight: 500;
    }
    .post-thumbnail{
        background-color: var(--bs-gray-200);
        height: 365px;
    }
    .text-heart{
        color: #F0464F;
    }
    .post-card{
        border-radius: 12px;   
    }
     .post-image-card {
        width: 100%;
        max-width: 500px;
        height: 400px;
        margin: 0 auto;
        border-radius: 12px;
        margin-top: 70px;
        border: 1px solid var(--bs-gray-200);
    }

    .post-image-card__content {
        background: var(--bs-gray-200);
        border-radius: 12px;
    }

    .post-image-card__content img {
        width: 65px;
        height: 65px;
    }
    .write-ai{
        background: linear-gradient(180deg, #654985, #E16490);
        bottom: 5px;
        right: 10px;
        font-size: 11px;
    }
    .postScheduler__media_box{
        width: 67%;
    }
    .postScheduler__preview{
        width: 33%;
    }

    .radio-button input[type="radio"] {
    display: none; /* Hide the default radio */
    }

    .radio-button span {
    width: 16px;
    height: 16px;
    border: 2px solid #888;
    border-radius: 50%;
    position: relative;
    transition: all 0.3s ease;
    }

    .radio-button input[type="radio"]:checked + span {
    border-color: #ff4555; /* blue border when selected */
    }

    .radio-button input[type="radio"]:checked + span::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 8px;
    height: 8px;
    background-color: #ff4555;
    border-radius: 50%;
    transition: all 0.2s ease;
    }
    .postScheduler__media .email_gallery > div{
        height: calc(100vh - 246px);
        overflow-y: auto;
        padding: 0px 10px !important;
        margin: 0px -10px;
    }
    .postScheduler__media > .flex-grow-1{
        width: 40%;
        max-width: 100% !important;
    }
    .postScheduler__media .dropdown-menu{
        width: 100%;
    }
    .postScheduler__media .mail_media_.active:before{
        content: '';
        display: block;
        background-color: #ffffff52;
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        z-index: 1;
    }
    .postScheduler__media .email_gallery .mail_media_.active .active-check{
        z-index: 1;
    }
    .postScheduler__media .email_gallery img{
        // object-fit: cover;
    }
    .postScheduler__media .email_gallery .col-xxl-3{
        padding: 5px;
    }
    .post-card .post-thumbnail .tb_post_modal_media_slider,
    .post-card .post-thumbnail .splide__track{
        height: 100%;
    }
    .post-card .post-thumbnail .splide__arrows{
        display: none;
    }
    .post-card .post-thumbnail .tb_modal_slider_dots{
        bottom: -35px;
    }
    .post-card .post-thumbnail .splide__pagination__page:hover,
    .post-card .post-thumbnail .splide__pagination__page.is-active{
        background-color: #ff4555;
    }
    .post-card .post-thumbnail .tb_video__,
    .postmedia__upload_items .post-media .tb_video__{
        width: 100%;
        height: 100% !important;
    }
    .post-card .post-thumbnail img,
    .post-card .post-thumbnail .tb_video__ video,
    .postmedia__upload_items .post-media .tb_video__ video{
        width: 100%;
        height: 100%;
        object-fit: contain;
        // object-fit: cover;
    }
    .post-card .post-thumbnail img.placeholder_img{
        width: auto;
        height: auto;
        object-fit: contain;   
    }
    .postScheduler__close{
        top: 20px;
        right: 20px;
    }
    .post-media img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .post-media button{
        text-shadow: 0px 0px 10px #ccc;
    }
    .website__ .custom-card{
    height: calc(100vh - 100px);
    overflow: auto;   
    }
    .item-close{
        top: -5px;
        right: -5px;
        padding: 0px !important;
        width: 20px;
        height: 20px;
        font-size: 12px;
    }
    .t_f_ai_ico_text {
        left: 15px;
        position: absolute;
        top: 12px;
        transform: scale(1.3);
    }
    .t_f_ai_res_wrap .t_f_ai_prompt{
        padding: 5px 0px 0px 25px !important;
        background-color: #e7eaf5;
    }

    .t_f_ai_btn {
        font-size: 12px;
    }
    .write-ai.tb_disabled {
        opacity: .5;
        pointer-events: none;
    }
    .t_f_ai_input_wrap_ textarea{
        max-height: 75px;
        padding: 10px 10px 0px 30px;
        border: 0px;
        background-color: #f1f1f4;
        resize:none;
    }
    .t_f_ai_input_wrap2_ textarea{
        background-color: #e7eaf5;
        border: 0px;
        height: 20px;
        padding: 5px 5px 5px 30px;
        resize: none;
    }
    .t_f_ai_ico2{
        left: 26px;
        position: absolute;
        top: 22px;
        transform: scale(1.3);
        z-index: 1;
    }

    .t_f_ai_res_wrap textarea.form-control{
        font-size: 13px;
        resize:none;
        border:0px;
    }
    .ai_input_wrap .t_f_ai_res_wrap textarea.form-control{
        padding: 0px;
    }

    .t_from_textarea{
        min-height: 120px !important;
        resize: vertical;
    }
    .t_f_textarea {
        border: 1px solid #babfc3;
        border-radius: 3px;
        position: relative;
    }
    .t_f_textarea.ai_input_wrap {
        padding: 8px;
        position: relative;
    }
    .t_f_ai_ico {
        left: 10px;
        position: absolute;
        top: 9px;
        transform: scale(1.3);
        z-index: 1;
    }
    .postGallery_emptyicon{
        height: calc(100vh - 197px);
    }
    .postScheduler__media{
        height: calc(100vh - 194px);
    }
    .t_f_textarea_inner{
        border: 1px solid #e7eaf5;
        padding: 8px;
        border-radius: 4px;
    }
}
.strip-active .post-scheduler-container .aside__body > div.card-aside{
    height: calc(100vh - 185px);
}
.post-scheduler-container .t_f_ai_res_wrap{
    background-color: var(--bs-body-bg);
    background-clip: padding-box;
    border: var(--bs-border-width) solid var(--bs-border-color);
    border-radius: var(--bs-border-radius);
}
.post-scheduler-container .ai_input_wrap .t_f_ai_res_wrap{
    border: 0px;
    margin-bottom: 10px;
}
@media (min-width: 992px) { /* lg breakpoint */
    .post-scheduler-container{
        .border-lg-start {
            border-left: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important
        }
    }
   
}
@media (min-width: 992px) {
    .strip-active .post-scheduler-container{
        padding-top: 80px;
        .website__ .custom-card{
            height: calc(100vh - 130px);
        }
        .postScheduler__media{
            height: calc(100vh - 226px);
        }
        .postScheduler__media .email_gallery > div{
            height: calc(100vh - 300px);
        }   
    }
    
}
@media (max-width: 1600px) {
    .post-scheduler-container{
        .postScheduler__media_box{
            width: 60%;
        }
        .postScheduler__preview{
            width: 40%;
        }
    }
}
@media (max-width: 1280px) {
    .post-scheduler-container{
        .postScheduler__close{
            top: 10px;
            right: 80px;
        }
    }
}
@media (max-width: 992px) {
    .post-scheduler-container{
        .postScheduler__media .email_gallery > div,
        .postGallery_emptyicon,
        .postScheduler__media{
            height: auto;
        }
        .postScheduler__media_box,
        .postScheduler__preview{
            width: 100%;
        }
    }
}
@media (max-width: 767px) {
    .social-publish-box{
        table{
            width: 767px;
        }
    }
}





    .t_f_skel_wrap {
        bottom: 0;
        height: 100%;
        left: 0;
        padding: 15px;
        position: absolute;
        right: 0;
        top: 0;
        width: 100%;
        z-index: 1;
        background-color: var(--bs-body-bg);
    }


    .t_f_s_bar_wrap {
        border-radius: 6px;
        height: 12px;
        margin-bottom: 10px;
        overflow: hidden;
        width: 100%;
    }


.t_f_s_bar {
    animation: tb_grad_animation 2.1s infinite linear;
    // background: -webkit-linear-gradient(315deg, #d3e3fd, #84c4ff, #a8c7fa, #84c4ff, #d3e3fd, #d3e3fd, #84c4ff, #a8c7fa, #84c4ff, #d3e3fd);
    // background: linear-gradient(135deg, #d3e3fd, #84c4ff, #a8c7fa, #84c4ff, #d3e3fd, #d3e3fd, #84c4ff, #a8c7fa, #84c4ff, #d3e3fd);
    background: -webkit-linear-gradient(315deg, #e1e1e1 25%, #f1f1f4 50%, #f1f1f4 75%);
    background: linear-gradient(135deg, #e1e1e1 25%, #f1f1f4 50%, #f1f1f4 75%);
    background-size: 800% 800%;
    width: 100%; /* Add width and height for demonstration */
    height: 100%;
}
@keyframes tb_grad_animation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}