.platform__icons {
    li {
        display: inline-block;
        vertical-align: middle;
        margin: 0 3px;
        position: relative;

        ul {
            position: absolute;
            background-color: var(--bs-body-bg);
            padding: 8px;
            min-width: 170px;
            box-shadow: 0 0 12px rgba(0, 0, 0, .1);
            border-radius: 3px;
            border: 1px solid var(--bs-border-color);
            right: -18px;
            left: auto;
            top: 110%;
            opacity: 0;
            visibility: hidden;
            transition: all 0.3s ease;
            margin: 10px auto;
            text-align: center;
            z-index: 9;
        }

        &:hover {
            ul.platform__more_ {
                top: 100%;
                opacity: 1;
                visibility: visible;
                transition: all 0.3s ease;
            }
        }

        img {
            width: 16px;
            height: auto;
        }
    }
}

