.rating-container {
    // padding: 1.5rem;

    .rating-summar {
        h1 {
            color: #333;
            font-size: 46px;
            font-weight: 500;
        }

        .m_rating {

            .star-wrapper {
                display: inline-block;
                height: 14px;
                position: relative;
                width: 20px;
            }

            span {
                color: #fbbc05;
                font-size: 18px;
                margin: 0 2px;
            }

            .m_post_rating.full-star {
                color: #ddd;
            }

            .filled-star {
                overflow: hidden;
                white-space: nowrap;
            }

            .filled-star,
            .full-star {
                left: 0;
                position: absolute;
                top: 0;
            }
        }
    }

    .rating-breakdown {
        max-width: 500px;
        margin: -5px 0;

        .rating-row {
            align-items: center;
            display: flex;
            justify-content: space-between;
            margin: 10px 0;
            margin-bottom: 20px;

            span {
                &:first-child {
                    color: var(--bs-gray-600);
                    font-size: 14px;
                    font-weight: 500;
                    text-align: left;
                    width: 46px;
                }

                &:last-child {
                    color: var(--bs-gray-800);
                    font-size: 14px;
                    font-weight: 600;
                    padding-left: 5px;
                    text-align: left;
                    width: 100px;
                }
            }

            .bar {
                height: 15px;
                border-radius: 0;
                background: var(--bs-gray-200);
                flex: 1 1;
                height: 15px;
                margin: 0 10px;
                overflow: hidden;

                .filled {
                    background: #fbbc05;
                    border-radius: 0;
                    height: 100%;
                }
            }
        }
    }

    .cta {
        color: #637079;
        font-size: 14px;
        font-weight: 500;
        margin-bottom: 0;
        margin-top: 25px;
        text-align: left;

        a {
            text-decoration: underline;
        }
    }
}



