    overflow: hidden;
    position: relative;
}

/*  */

.sk_img:before,
.sk_line:before,
.sk_rect:before {
    animation: shimmer 1s linear infinite;
    background-image: linear-gradient(90deg, #d9d9d9 0, #0000000d 20%, #d9d9d9 40%, #d9d9d9);
    background-repeat: no-repeat;
    background-size: 450px 400px;
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
}

@keyframes shimmer {
    0% {
        background-position: -450px 0;
    }

    100% {
        background-position: 450px 0;
    }
}

