.input-spinner {
    position: absolute;
    top: calc(50% - .6rem);
    border-radius: 50% !important;
    width: 1.3rem;
    height: 1.3rem;
    left: auto;
    right: 15px;
    z-index: 1;
    border: 1.5px solid var(--bs-gray-300);
    border-right: 1.5px solid transparent;
    animation: animation-spinner 0.5s linear infinite;
}

.spinner {
    width: auto;
    padding-right: 2.7rem;
    position: relative;
    color: var(--bs-btn-disabled-color);
    background-color: var(--bs-btn-disabled-bg);
    border-color: var(--bs-btn-disabled-border-color);
    opacity: var(--bs-btn-disabled-opacity);
}

.spinner:before {
    content: "";
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    border-radius: 50%;
    width: 1.3rem;
    height: 1.3rem;
    margin-top: -0.65rem;
    left: auto;
    right: .9rem;
    z-index: 1;
    border: 1.5px solid var(--bs-btn-color);
    border-right: 1.5px solid transparent;
    animation: animation-spinner 0.5s linear infinite;
}

.spinner.btn-sm {
    padding-right: 2.3rem;
}

.spinner-dark.spinner:before {
    border: 1.5px solid var(--bs-primary);
    border-right: 1.5px solid transparent;
}

.spinner-gray.spinner:before {
    border: 1.5px solid var(--bs-gray-500);
    border-right: 1.5px solid transparent;
}

.spinner.btn-sm:before,
.spinner.spinner-sm:before {
    width: 18px;
    height: 18px;
    margin-top: -9px;
    right: 10px;
}

.spinner.btn-icon {
    padding: 0;
}

.spinner.btn-icon:before {
    right: 6px;
}

#inputSpinner.spinner.spinner-sm:before {
    right: 46px;
    z-index: 7;
}

.spinner.btn-xs:before,
.spinner.spinner-xs:before {
    width: 15px;
    height: 15px;
    margin-top: -9px;
    right: 10px;
}

.btn.spinner {
    pointer-events: none;
}

@-webkit-keyframes animation-spinner {
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes animation-spinner {
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

