.container-network-manager{
    padding-top: 70px;
    .dropdown-toggle{
        background-color: var(--bs-secondary-200);
    }
    .page-item{
        .page-link{
            border-radius: 6px;
        }
    }
    .table-pagination{
        outline: none !important;
        display: flex;
        height: 32px;
        width: 32px;
        justify-content: center;
        align-items: center;
        border-radius: 6px;
        margin-left: 6px;
    }
    .loader_center .loader__{
        height: 100% !important;
    }
    .dropdown-menu {
        .dropdown-item{
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }        
        }
    }
    .defaultSelected-gallery{
        border: 1px solid var(--bs-border-color);
        background-color: var(--bs-secondary-200);
    }
}
