.shop-apps .theme_preview{
    height: calc(116.7vh - 364px);
}

@media (max-height: 2000px) {
    .shop-apps .preview_.p_header_ .card-body.card-body-download {
        height: calc(100vh - 196px);
    }
}

@media (min-width: 992px) {
    .app-page.has-subheader.shop-apps {
        padding-top: 0;
    }

    .shop-apps .custom-side-menu .subheader {
        width: calc(100% - 185px);
    }
}

@media (min-width: 768px) {
    .shop-apps .custom-side-menu .subheader {
        width: calc(100% - 185px);
    }
}

@media (max-width: 767px) {
    .shop-apps .custom-side-menu .subheader {
        width: calc(100% - 135px);
    }
}

@media (max-width: 479px) {
    .shop-apps .custom-side-menu .subheader {
        width: calc(100% - 50px);
    }
}

@media (max-width: 992px) {
    .shop-apps .container-social-accounts .card-body{
        max-height: 100% !important;
    }

    .shop-apps[data-name=analytics] .review-bnn + .subheader {
        top: 0px;
    }

    .shop-apps[data-name=analytics] .review-bnn+.subheader+.full-content {
        padding-top: 12px;
    }
}

@media screen and (max-width:1366px){
    .shop-apps .rule_action .position-sticky {
        position: static !important;
    }

    .shop-apps .add_more_rule .dropdown-menu, .shop-apps .more_actions {
        left: -40px !important;
    }
}
